.login {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;

	&_photo {
		width: 50%;
		height: 100%;
		background-image: url('https://cdn.pixabay.com/photo/2015/08/25/03/50/herbs-906140_1280.jpg');
		background-size: cover;
		background-position: center;

	}
	
	&_section {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 20px;
		background-color: #f9fafc;;
		width: 50%;
		height: 100%;

		h1 {
			padding: 20px;
		}

		&_form {
            display: flex;
            flex-direction: column;
			padding: 20px;

			&_error {
				display: none;
			}

			&_item {
                display: flex;
                justify-content: center;
                align-items: center;
				input {
					margin: 10px;
					padding: 10px;
				}

			}
		}
	}
}
